import {mapState} from 'vuex';
import chatName from '@/components/chats/assets/name.vue'
import chatTime from '@/components/chats/assets/time.vue'
import chatIcon from '@/components/chats/assets/icon.vue'

import event from '@/components/events/event/index.vue'

export default {
  name: 'chatsPreview',
  props: {
    chat: Object,
    dummy: Boolean
  },

  components: {
    chatName,
    chatTime,
    chatIcon,

    event
  },

  data: function () {

    return {
      loading: false,
      roomMuted: false
    }

  },

  computed: mapState({
    auth: state => state.auth,

    m_chat: function () {
   let pushRules = this.core.mtrx.client._pushProcessor.getPushRuleById(this.chat.roomId)
      if (pushRules !== null) {
        this.roomMuted = true
      }
      if (this.chat.roomId) {

        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)

        return m_chat || {}
      }

    },

    event: function () {
      if (this.chat.roomId) {
        var events = ((this.m_chat || {}).timeline || [])
        if (events.length) {
          return events[events.length - 1]
        }
      }


    },

    ...mapState([
      'minimized',
      'active'
    ]),
  }),

  methods: {},
}